.coming-soon {
  background-color: #45B5AA;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
}

.coming-soon .logo {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 250px;
  padding-bottom: 20px;
  -webkit-filter: brightness(100);
          filter: brightness(100);
}

.coming-soon .text {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 180px;
  padding-bottom: 50px;
}

.coming-soon .mascot {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 270px;
  -webkit-transform: translateX(-50vw) translateX(150px) translateY(100px);
      -ms-transform: translateX(-50vw) translateX(150px) translateY(100px);
          transform: translateX(-50vw) translateX(150px) translateY(100px);
}

.coming-soon .copy-right {
  font-size: 12px;
  -webkit-transform: translateX(-50vw) translateX(175px) translateY(100px);
      -ms-transform: translateX(-50vw) translateX(175px) translateY(100px);
          transform: translateX(-50vw) translateX(175px) translateY(100px);
}

@media all and (min-width: 600px) {
  .coming-soon .mascot {
    -webkit-transform: translateX(-145px)  translateY(100px);
        -ms-transform: translateX(-145px)  translateY(100px);
            transform: translateX(-145px)  translateY(100px);
  }

  .coming-soon .copy-right {
    -webkit-transform: translateX(-120px)  translateY(100px);
        -ms-transform: translateX(-120px)  translateY(100px);
            transform: translateX(-120px)  translateY(100px);
  }
}

.coming-soon p {
  font-family: Lato;
  color: white;
  font-weight: 300;
  font-size: 24px;
}


