.result-container {
  padding-top: 100px;
  position: relative; }
  .result-container .result-card {
    border-radius: 20px;
    background-color: white;
    padding: 30px 50px;
    margin: 10px 0;
    border-width: 2px;
    border-style: solid;
    border-color: transparent;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease; }



